<template>
    <div class="container">
        <!-- 列板块一 -->
        <div class="column">
            <div class="UserInfo Pannelborder">
                <div class="my-row">
                    <div class="col_1">
                        <el-avatar :src="avatarUrl" :size="avatarSize" style="border: solid aqua 2px;" />
                    </div>
                    <div class="col_2">
                        <div class="user-details-cert" v-if="User" style="font-weight: bold;color: aqua;">
                            {{User.basic_info.name}}</div>
                        <div class="user-details-cert" v-if="User">认证状态：{{User.basic_info.state}}</div>
                        <div class="user-details-device" v-if="User">使用设备：{{User.basic_info.device}}</div>
                        <div class="user-details-fans" v-if="User">粉丝：{{User.basic_info.fans}}</div>
                        <div class="user-details-follow" v-if="User">关注：{{User.basic_info.guanzhu}}</div>
                    </div>
                </div>
                <div class="user-info-bottom" style="margin-bottom: .0625rem;">
                    <div class="user-info-bottom-left" v-if="User.basic_info.juzi">{{ User.basic_info.juzi[0] }}</div>
                    <div class="user-info-bottom-right" v-if="User.basic_info.juzi">{{ User.basic_info.juzi[1] }}</div>
                </div>
                <div class="panel-footer"></div>
            </div>
            <DataChart :chartTitle="`轨迹追踪`" :chartOption="option_3" :chartId="`chart_3`" />
            <DataChart :chartTitle="`兴趣词云展示`" :chartOption="UserInterest" :chartId="`chart_19`"
                :imageHeight="`255px`" />
        </div>
        <!-- 列板块二 -->
        <div class="column_center">
            <div class="no">
                <div class="no-hd">
                    <ul>
                        <li v-if="User.hudong">{{User.hudong}}</li>
                        <li v-else>{{'33.33%'}}</li>
                        <li v-if="User.yingxiangli">{{User.yingxiangli}}</li>
                        <li v-else>{{'66.75'}}</li>
                    </ul>
                </div>
                <div class="no-bd">
                    <ul>
                        <li>互动率（User Interaction Rate）/ 天</li>
                        <li>影响力评分（Influence Score）/ 天</li>
                    </ul>
                </div>
            </div>
            <div class="map" style="margin-top: 10px;">
                <div class="map1"></div>
                <div class="map2"></div>
                <div class="map3"></div>
                <DataChart :chartOption="UserNetwork" :chartId="`chart_7`" :imageHeight="`595px`" />
            </div>
        </div>
        <!-- 列板块三 -->
        <div class="column">
            <div class="UserPosts Pannelborder">
                <el-table :data="tableData" height="8.2rem" border ref="table" :header-cell-style="getRowClass">
                    <el-table-column prop="date" label="日期" width="120">
                    </el-table-column>
                    <el-table-column prop="text" label="帖子"> </el-table-column>
                </el-table>
                <div class="panel-footer"></div>
            </div>
            <DataChart :chartTitle="`用户情感分析`" :chartOption="UserSentimentOption" :chartId="`chart_6`"
                :imageHeight="`255px`" />
        </div>
    </div>
</template>

<script setup>
    import DataChart from '@/components/DataChartPx.vue'
    import { ref, reactive, shallowRef, onMounted, onBeforeUnmount, watch } from 'vue'
    import configOption from '@/ChartOption/optionConfig.js'
    import UserInfo from '@/assets/data/user-image.json'

    const option_1 = shallowRef(configOption.option_1)
    const option_2 = shallowRef(configOption.option_2)
    //折线图
    const option_3 = reactive(configOption.option_3)
    //词云
    const UserInterest = reactive(configOption.UserInterest)

    const option_4 = shallowRef(configOption.option_4)
    const option_5 = shallowRef(configOption.option_5)
    const option_6 = shallowRef(configOption.option_6)
    const UserNetwork = reactive(configOption.UserNetwork)
    const UserSentimentOption = reactive(configOption.UserSentimentOption)
    const tableData = shallowRef(configOption.tableData);

    const User = shallowRef({
        basic_info: {
            name: '苏炳添',
            state: '已认证',
            device: '移动端',
            fans: "492.9万",
            guanzhu: "102",
            hudong: '33.33%',
            yingxiangli: '66.43%',
            juzi: ["V指数体育52.52分", "阅读数1万+"]
        }
    })

    // 生成随机的X坐标，假设范围是在 [minX, maxX] 内
    function generateRandomXCoordinate(minX, maxX) {
        return Math.floor(Math.random() * (maxX - minX + 1)) + minX;
    }

    // 生成随机的Y坐标，假设范围是在 [minY, maxY] 内
    function generateRandomYCoordinate(minY, maxY) {
        return Math.floor(Math.random() * (maxY - minY + 1)) + minY;
    }


    const avatarUrl = ref(`https://weibo-zby.obs.cn-north-4.myhuaweicloud.com/subingtian.jpg`)
    const avatarSize = ref(150)


    // 定时更换对象信息
    const timerInstance = shallowRef(null)
    const infoIndex = ref(0)
    onMounted(() => {
        if (timerInstance.value) { clearInterval(timerInstance.value) }
        timerInstance.value = setInterval(() => {
            changeUserInfo(infoIndex.value)
        }, 5000)
    })
    onBeforeUnmount(() => {
        if (timerInstance.value) { clearInterval(timerInstance.value) }
    })

    function changeUserInfo(index) {
        infoIndex.value = (infoIndex.value + 1) % UserInfo.length
        User.value = UserInfo[index]
        avatarUrl.value = User.value.basic_info.image
        option_3.series[0].data = User.value.guiji.fatieshuliang
        option_3.series[1].data = User.value.guiji.pinglunshuliang
        option_3.series[2].data = User.value.guiji.zhuanfashuliang
        tableData.value = User.value.tiezi
        UserInterest.series[0].data = User.value.yonghuxingqu

        // 然后修改用户的情感
        UserSentimentOption.series[0].data[0].value = [Math.floor(Math.random() * (100 - 10 + 1)) + 66, Math.floor(Math.random() * (100 - 10 + 1)) + 7, Math.floor(Math.random() * (100 - 10 + 1)) + 57, Math.floor(Math.random() * (100 - 10 + 1)) + 28, Math.floor(Math.random() * (100 - 10 + 1)) + 17, Math.floor(Math.random() * (100 - 10 + 1)) + 14]

        // 然后修改用户所在的关系网络
        UserNetwork.series[0].data = UserNetwork.series[0].data.map(function (item) {
            return {
                name: item.name,
                x: generateRandomXCoordinate(0, 1000),
                y: generateRandomYCoordinate(0, 800),
                symbol: item.symbol
            }
        })
        console.log(UserNetwork)
    }

    //改变表格的样式
    function getRowClass({ row, column, rowIndex, columnIndex }) {
        return {
            background: '#03487B',
            color: '#1adafb'
        }
    }

</script>

<style lang="scss" scoped>
