<template>
    <el-empty>
        <el-button type="primary" @click="backToCenter">返回主页</el-button>
    </el-empty>
</template>

<script setup>
    import { useRouter } from 'vue-router'
    const router = useRouter()

    function backToCenter() {
        router.push({ name: 'center' })
    }
</script>

<style scoped>
    .el-empty {
        position: absolute;
        left: 50%;
        top: 40%;
        transform: translate(-50%, -50%);
    }
</style>