import Center from '../views/Center.vue'
import DataView from '../views/DataView.vue'
import UserImage from '../views/UserImage.vue'
import HotpotAnalyze from '../views/HotpotAnalyze.vue'
import CommunityDiscovery from '../views/CommunityDiscovery.vue'
import Networking from '../views/Networking.vue'
import TestView from '../views/Test.vue'

export default [
    {
        path: 'center',
        component: Center
    },
    {
        path: 'test',
        component: TestView
    },
    {
        path: 'console',
        component: DataView
    },
    {
        path: 'user-image',
        component: UserImage
    },
    {
        path: 'hotpot-analyze',
        component: HotpotAnalyze
    },
    {
        path: 'commutity-discovery',
        component: CommunityDiscovery
    },
    {
        path: 'networking',
        component: Networking
    },
]