<template>
    <div class="panel" style="border:solid 1px #08bfed" :style="{'height': imageHeight,'width' : imageWidth}">
        <h2 v-if="chartTitle">{{chartTitle}}</h2>
        <div :id="chartId" class="chart" :style="{'height': imageHeight}" style="width: 100%;"></div>
        <div class="panel-footer"></div>
    </div>
</template>

<script setup>
    import { onMounted, ref, onBeforeUnmount, shallowRef, watch } from 'vue'
    import * as echarts from 'echarts'
    import 'echarts-wordcloud'
    const props = defineProps(['chartTitle', 'chartOption', 'chartId', 'imageHeight', 'imageWidth'])

    const chartInstance = shallowRef(null);

    const imageHeight = ref(props.imageHeight)

    onMounted(() => {
        const chartContainer = document.getElementById(props.chartId);

        if (chartInstance.value) { chartInstance.value.dispose(); }

        chartInstance.value = echarts.init(chartContainer);

        chartInstance.value.setOption(props.chartOption);

        //4. 让图表跟随屏幕自适应:// 调用 ECharts 实例的 resize 方法
        window.addEventListener('resize', function () {
            if (chartInstance.value) {
                chartInstance.value.resize();
            }
        })
    })

    onBeforeUnmount(() => {
        window.removeEventListener('resize', function () {
            if (chartInstance.value) {
                chartInstance.value.resize();
            }
        });
        if (chartInstance.value) {
            chartInstance.value.dispose();
        }
    });

    watch(() => props.chartOption, (newValue, oldValue) => {
        chartInstance.value.setOption(props.chartOption);
    }, { deep: true })

</script>

<style lang="scss" scoped>
    .panel {
        position: relative;
        height: 4.125rem;
        padding: 0 0;
        margin-bottom: .1875rem;
        border: 1px solid rgba(25, 186, 139, 0.17);
        background: url(../assets/line\(1\).png) rgba(255, 255, 255, 0.03);
        background-size: 100% 100%;

        /* 设置盒子的两个角的样式 */
        &::before {
            position: absolute;
            top: 0;
            left: 0;
            width: 10px;
            height: 10px;
            border-top: 2px solid #02a6b5;
            border-left: 2px solid #02a6b5;
            content: "";
        }

        &::after {
            position: absolute;
            top: 0;
            right: 0;
            width: 10px;
            height: 10px;
            border-top: 2px solid #02a6b5;
            border-right: 2px solid #02a6b5;
            content: "";
        }

        /* 底部的两个角的样式设计 */
        .panel-footer {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;

            &::before {
                position: absolute;
                left: 0;
                bottom: 0;
                width: 10px;
                height: 10px;
                border-left: 2px solid #02a6b5;
                border-bottom: 2px solid #02a6b5;
                content: "";
            }

            &::after {
                position: absolute;
                right: 0;
                bottom: 0;
                width: 10px;
                height: 10px;
                border-right: 2px solid #02a6b5;
                border-bottom: 2px solid #02a6b5;
                content: "";
            }
        }
    }

    h2 {
        height: 0.6rem;
        line-height: 0.6rem;
        text-align: center;
        color: #fff;
        font-size: 20px;
        font-weight: 400;
    }

    .chart {
        height: 3rem;
    }
</style>