<template>
    <div class="container">
        <NetWorkChart v-if="showChart" :chartOption="option" :chartId="`chart_10`" :chartHeight="12.5">
        </NetWorkChart>
    </div>
</template>

<script setup>
    import { reactive, ref, onMounted } from 'vue'
    import NetWorkChart from '@/components/NetWorkChart.vue'
    import optionConfig from '@/ChartOption/optionConfig.js'
    import { ElMessage } from 'element-plus'
    const option = reactive(optionConfig.networkOption)
    const showChart = ref(false)


    function loadAction() {
        ElMessage({
            message: '数据加载完毕',
            type: 'success',
        })
    }

    onMounted(() => {
        // 模拟数据加载完成后显示图表
        setTimeout(() => {
            showChart.value = true
        }, 1000)

        setTimeout(() => {
            loadAction()
        }, 1000)
    })


</script>

<style lang="scss" scoped>
    .container {
        background: url('@/assets/newbg_1.png') no-repeat top center;
        height: 100%;
        width: 100%;
        transform: translateZ(0);
    }
</style>