<template>
    <div class="content panel">
        <div class="map1"></div>
        <div class="map2"></div>
        <div class="map3"></div>
        <div ref="charts" style="width: 15rem; height: 11.45rem; margin: 0 auto;"></div>
        <div class="panel-footer"></div>
    </div>
</template>

<script>
    import * as echarts from 'echarts'
    import china from '@/assets/china_1.json'
    export default {
        mounted() {
            this.initCharts()
        },
        methods: {
            initCharts() {
                const charts = echarts.init(this.$refs['charts'])
                const dataList = [
                    { name: "南海诸岛", value: 0 },
                    { name: '北京', value: 54 },
                    { name: '天津', value: 13 },
                    { name: '上海', value: 40 },
                    { name: '重庆', value: 75 },
                    { name: '河北', value: 13 },
                    { name: '河南', value: 83 },
                    { name: '云南', value: 11 },
                    { name: '辽宁', value: 19 },
                    { name: '黑龙江', value: 15 },
                    { name: '湖南', value: 69 },
                    { name: '安徽', value: 60 },
                    { name: '山东', value: 39 },
                    { name: '新疆', value: 4 },
                    { name: '江苏', value: 31 },
                    { name: '浙江', value: 104 },
                    { name: '江西', value: 36 },
                    { name: '湖北', value: 1052 },
                    { name: '广西', value: 33 },
                    { name: '甘肃', value: 7 },
                    { name: '山西', value: 9 },
                    { name: '内蒙古', value: 7 },
                    { name: '陕西', value: 22 },
                    { name: '吉林', value: 4 },
                    { name: '福建', value: 18 },
                    { name: '贵州', value: 5 },
                    { name: '广东', value: 98 },
                    { name: '青海', value: 1 },
                    { name: '西藏', value: 0 },
                    { name: '四川', value: 44 },
                    { name: '宁夏', value: 4 },
                    { name: '海南', value: 22 },
                    { name: '台湾', value: 3 },
                    { name: '香港', value: 5 },
                    { name: '澳门', value: 5 }]
                const option = {
                    // backgroundColor:'#0e2152',

                    tooltip: {
                        triggerOn: "click",
                        formatter: function (e, t, n) {
                            return .5 == e.value ? e.name + "：有疑似病例" : e.seriesName + "<br />" + e.name + "：" + e.value
                        },
                    },
                    visualMap: {
                        min: 0,
                        max: 2000,
                        left: 26,
                        bottom: 40,
                        showLabel: true,
                        // text: ["高", "低"],
                        pieces: [
                            {
                                gt: 100,
                                label: "> 10000 人",
                                color: "#7f1100"
                            },
                            {
                                gte: 10,
                                lte: 100,
                                label: "3000 - 1000 人",
                                color: "#ff5428"
                            },
                            {
                                gte: 1,
                                lt: 10,
                                label: "1000 - 3000 人",
                                color: "#ff8c71"
                            },
                            {
                                gt: 0,
                                lt: 1,
                                label: "100 - 1000 人",
                                color: "#ffd768"
                            },
                            {
                                value: 0,
                                label:'< 100 人',
                                color: "green"
                            }],
                        textStyle: {
                            color: '#ffffff'
                        },
                        show: !0
                    },
                    geo: {
                        map: "china",
                        roam: false,
                        layoutCenter: ['50%', '50%'], // 设置地图中心位置
                        layoutSize: '100%', // 设置地图大小
                        scaleLimit: {
                            min: 1,
                            max: 2
                        },
                        zoom: 1.23,
                        top: 120,
                        label: {
                            normal: {
                                show: !0,
                                fontSize: "14",
                                color: "#ff3"
                            }
                        },
                        itemStyle: {
                            normal: {
                                shadowBlur: 50,
                                shadowColor: 'rgba(0, 0, 0, 0.2)',
                                borderColor: "rgba(0, 0, 0, 0.2)",
                            },
                            emphasis: {
                                areaColor: "#f2d5ad",
                                shadowOffsetX: 0,
                                shadowOffsetY: 0,
                                borderWidth: 0
                            }
                        }
                    },
                    series: [{
                        name: "确诊病例",
                        type: "map",
                        geoIndex: 0,
                        data: dataList,
                        visualMap: "visualMap",
                    }]
                }
                echarts.registerMap('china', china)
                charts.setOption(option)
            }
        },
    }
</script>

<style lang="scss" scoped>
    .content {
        height: 100%;
    }

    .panel {
        position: relative;
        height: 100%;
        /* margin-bottom: .1875rem; */
        border: 1px solid rgba(25, 186, 139, 0.17);
        background-size: 100% 100%;

        /* 设置盒子的两个角的样式 */
        &::before {
            position: absolute;
            top: 0;
            left: 0;
            width: 10px;
            height: 10px;
            border-top: 2px solid #02a6b5;
            border-left: 2px solid #02a6b5;
            content: "";
        }

        &::after {
            position: absolute;
            top: 0;
            right: 0;
            width: 10px;
            height: 10px;
            border-top: 2px solid #02a6b5;
            border-right: 2px solid #02a6b5;
            content: "";
        }

        /* 底部的两个角的样式设计 */
        .panel-footer {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;

            &::before {
                position: absolute;
                left: 0;
                bottom: 0;
                width: 10px;
                height: 10px;
                border-left: 2px solid #02a6b5;
                border-bottom: 2px solid #02a6b5;
                content: "";
            }

            &::after {
                position: absolute;
                right: 0;
                bottom: 0;
                width: 10px;
                height: 10px;
                border-right: 2px solid #02a6b5;
                border-bottom: 2px solid #02a6b5;
                content: "";
            }
        }
    }

    .map {
        height: 10.125rem;
        position: relative;

        .chart {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 10.125rem;
        }
    }

    .map1 {
        width: 10rem;
        height: 10rem;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: url(@/assets/map.png);
        background-size: 100% 100%;
        opacity: .3;
    }

    .map2 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 10rem;
        height: 10rem;
        background: url(@/assets/lbx.png);
        background-size: 100% 100%;
        animation: rotate1 15s linear infinite;
        opacity: 0.6;
    }

    @keyframes rotate1 {
        from {
            transform: translate(-50%, -50%) rotate(0deg);
        }

        to {
            transform: translate(-50%, -50%) rotate(360deg);
        }
    }

    @keyframes rotate2 {
        from {
            transform: translate(-50%, -50%) rotate(0deg);
        }

        to {
            transform: translate(-50%, -50%) rotate(-360deg);
        }
    }

    @media screen and (max-width: 1024px) {
        html {
            font-size: 42px !important;
        }
    }

    @media screen and (min-width: 1920px) {
        html {
            font-size: 80px !important;
        }
    }
</style>