<template>
    <div class="netWorkContainer" :style="{'height': chartHeight + 'rem'}">
        <div :id="chartId" style="height: 100%; width: 100%;"></div>
    </div>
</template>

<script setup>
    import { onMounted, onBeforeUnmount, shallowRef, watch } from 'vue'
    import * as echarts from 'echarts'
    const props = defineProps(['chartOption', 'chartId', 'chartOption', 'chartHeight'])

    const chartInstance = shallowRef(null)

    onMounted(() => {
        const chartContainer = document.getElementById(props.chartId)
        if (chartInstance.value) { chartInstance.value.dispose() }

        chartInstance.value = echarts.init(chartContainer)
        chartInstance.value.setOption(props.chartOption);

        window.addEventListener('resize', function () {
            if (chartInstance.value) {
                chartInstance.value.resize()
            }
        })
    })

    watch(() => props.chartOption, (newValue, oldValue) => {
        chartInstance.value.setOption(props.chartOption);
    }, { deep: true })

</script>