<template>
    <div class="container" style="height: 100%;">
        <!-- 顶部方框 -->
        <div class="row mb-4 justify-content-center"
            style="display: flex; justify-content: flex-start; margin-top: 40px;">

            <div class="col-md-3" style="margin-right: 25px;">
                <div class="border rounded p-3 text-center"
                    style="border: 1px solid #ccc; background-color: #f8f9fa; width: 150px; height: 100px; display: flex; justify-content: center; align-items: center;">
                    <div style="padding: 10px;">
                        <h2 class="mb-2" style="font-size: 20px; color: #555;">今日爬取</h2>
                        <p style="font-size: 34px; color: #000; text-align: center;">{{ crawledCount }}</p>
                        <!-- 捏造的数据 -->
                    </div>
                </div>
            </div>

            <div class="col-md-3" style="margin-right: 25px;">
                <div class="border rounded p-3 text-center"
                    style="border: 1px solid #ccc; background-color: #f8f9fa;width: 150px; height: 100px; display: flex; justify-content: center; align-items: center;">
                    <div style="padding: 20px;">
                        <h2 class="mb-2" style="font-size: 20px; color: #555;">历史爬取</h2>
                        <p style="font-size: 34px; color: #000; text-align: center;">{{historyCount}}</p> <!-- 捏造的数据 -->
                    </div>
                </div>
            </div>
            <div class="col-md-3" style="margin-right: 25px;">
                <div class="border rounded p-3 text-center"
                    style="border: 1px solid #ccc; background-color: #f8f9fa;width: 150px; height: 100px; display: flex; justify-content: center; align-items: center;">
                    <div style="padding: 10px;">
                        <h2 class="mb-2" style="font-size: 20px; color: #555;">当前参数</h2>
                        <p style="font-size: 34px; color: #000; text-align: center;">{{xcCount}}线程</p> <!-- 捏造的数据 -->
                    </div>
                </div>
            </div>
            <div class="col-md-3" style="margin-right: 25px;">
                <div class="border rounded p-3 text-center"
                    style="border: 1px solid #ccc; background-color: #f8f9fa;width: 150px; height: 100px; display: flex; justify-content: center; align-items: center;">
                    <div style="padding: 10px;">
                        <h2 class="mb-2" style="font-size: 20px; color: #555;">效率提升</h2>
                        <!-- 使用条件渲染来动态设置颜色 -->
                        <p style="text-align: center;"
                            :style="{ 'font-size': '34px', 'color': efficiency > 0 ? 'green' : 'red' } ">{{ efficiency
                            }}%
                        </p>
                    </div>
                </div>
            </div>
            <div class="col-md-3" style="margin-right: 25px;">
                <div class="border rounded p-3 text-center"
                    style="border: 1px solid #ccc; background-color: #f8f9fa;width: 150px; height: 100px; display: flex; justify-content: center; align-items: center;">
                    <div style="padding: 10px;">
                        <h2 class="mb-2" style="font-size: 20px; color: #555;">运行时间</h2>
                        <p style="font-size: 34px; color: #000; text-align: center;">{{totalTime}}h</p> <!-- 捏造的数据 -->
                    </div>
                </div>
            </div>
            <div class="col-md-3">
                <div class="border rounded p-3 text-center"
                    style="border: 1px solid #ccc; background-color: #f8f9fa; width: 150px; height: 100px; display: flex; justify-content: center; align-items: center;">
                    <div style="padding: 10px;">
                        <h2 class="mb-2" style="font-size: 20px; color: #555;">总成功率</h2>
                        <!-- 使用条件渲染来动态设置颜色 -->
                        <p :style="{ 'font-size': '34px', 'color': successmod > 0 ? 'green' : 'red' }">{{ successmod }}%
                        </p>
                    </div>
                </div>
            </div>
        </div>

        <!-- 分隔线 -->
        <!-- <hr class="divider" /> -->

        <!-- 中间部分 -->
        <div class="row mb-4" style="display: flex; justify-content: center; margin-top: 40px;">
            <!-- 今日爬取数据展示 左半部分 -->
            <div class="col-md-6 " style="display: flex; flex-direction:column;justify-content: flex-start;">

                <!-- 阶梯瀑布图 -->
                <div class="visit-chart" style="margin-top: 20px;">
                    <!-- <h3 class="mb-3" style="font-size: 18px;">十四天内的访问量变化</h3> -->
                    <div ref="visitChart" style="width: 100%; height: 300px;"></div>
                </div>


                <div class="crawler-data-display ">
                    <!-- 折线统计图 -->
                    <div class="line-chart " style="margin-right:40px;">
                        <div ref="lineChart" style="width: 100%; height: 300px;"></div>
                    </div>

                    <div class="user-list-container">
                        <h3 class="mb-3" style="margin:5px;font-size: 18px;">最近爬取的用户列表</h3>
                        <div class="user-list" ref="userList" @mouseenter="stopScroll" @mouseleave="startScroll"
                            style="margin-right:50px;">
                            <table class="table">
                                <thead>
                                    <tr>
                                        <th scope="col">用户</th>
                                        <th scope="col">类型</th>
                                        <th scope="col">时间</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(user, index) in recentUsers" :key="index">
                                        <td>{{ user.name }}</td>
                                        <td>{{ user.type }}</td>
                                        <td>{{ user.time }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>

                </div>


                <div class="col-md-6 mb-4" style="margin-right: 10px; margin-top:50px;">
                    <div class="border rounded p-3">
                        <h3 class="mb-3 text-center" style="font-size: 18px; margin-bottom:6px;">今日爬取数据展示</h3>
                        <textarea class="form-control fixed-size " rows="15" cols="60"
                            readonly>{{ currentText }}</textarea>
                    </div>
                </div>

            </div>



            <!-- 图表部分 右半部分-->
            <div class="col-md-6 " style="display: flex; flex-direction: column; justify-content: flex-start;">

                <!-- 标题 -->
                <h2 class="mb-4 text-center" style="font-size:24px; padding:10px;">图表区域</h2>

                <div class="col-md-6 " style="display: flex; flex-direction: row; justify-content: center;">
                    <!-- 图表盒子 -->
                    <div class="col-md-6 mb-4" style="margin-right: 40px;">
                        <!-- 类型统计 -->
                        <div class="border rounded p-3 border-gray" style="padding:10px;">
                            <h3 class="mb-3 text-center gray-line" style="font-size: 18px;">今日爬取数据类型统计</h3>
                            <div ref="sectionDataChart" style="height: 300px;"></div>
                        </div>
                    </div>

                    <div class="col-md-6">
                        <!-- 过去几天爬取数据量趋势 -->
                        <div class="border rounded p-3 border-gray " style="padding:10px;">
                            <h3 class="mb-3 text-center gray-line" style="font-size: 18px;">七日内爬取数据量趋势</h3>
                            <div ref="pastDaysChart" style="height: 300px;"></div>
                        </div>
                    </div>
                </div>


                <div class="col-md-6 "
                    style="display: flex; flex-direction: row; justify-content: center; margin-top:20px;">

                    <!-- 数据量来源地址top10 -->
                    <div class="col-md-6 border-gray" style="padding: 10px;">
                        <div class="border rounded p-3">
                            <h3 class="mb-3 text-center gray-line" style="font-size: 18px;">数据来源省份 Top 10</h3>
                            <div ref="provinceChart" style="height: 300px;"></div>
                        </div>
                    </div>

                    <!-- 爬虫状态监控 -->
                    <div class="row justify-content-center border-gray" style="padding: 15px; margin-left:39px;">
                        <div class="col-md-12">
                            <div class="border rounded p-3 ">
                                <h3 class="mb-3 text-center gray-line" style="font-size: 18px;">爬虫状态监控</h3>
                                <p class="text-center custom-font-size">运行时间：{{ spiderStatus.runtime }}</p>
                                <p class="text-center custom-font-size">成功爬取数据量：{{ spiderStatus.successCount }}</p>
                                <p class="text-center custom-font-size">失败爬取数据量：{{ spiderStatus.errorCount }}</p>
                            </div>
                        </div>
                    </div>

                </div>

                <div class="col-md-6 "
                    style="display: flex; flex-direction: row; justify-content: center; margin-top:20px; margin-bottom:100px;">
                    <!-- 爬取数据量 Top N 板块 -->
                    <div class="col-md-6 border-gray" style="padding:10px;">
                        <div class="border rounded p-3">
                            <h3 class="mb-3 text-center gray-line" style="font-size: 18px;">爬取数据量Top N板块</h3>
                            <div ref="topSectionsChart" style="height: 300px;"></div>
                        </div>
                    </div>

                    <!-- 爬虫状态监控 -->
                    <div class="row justify-content-center border-gray" style="padding: 15px; margin-left:39px;">
                        <div class="col-md-12">
                            <div class="border rounded p-3 ">
                                <h3 class="mb-3 text-center gray-line" style="font-size: 18px;">爬虫状态监控</h3>
                                <p class="text-center custom-font-size">运行时间：{{ spiderStatus.runtime }}</p>
                                <p class="text-center custom-font-size">成功爬取数据量：{{ spiderStatus.successCount }}</p>
                                <p class="text-center custom-font-size">失败爬取数据量：{{ spiderStatus.errorCount }}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import * as echarts from 'echarts';

    export default {
        data() {
            return {

                spiderStatus: {
                    runtime: '5小时30分钟',
                    successCount: 5000,
                    errorCount: 200
                },
                efficiency: 20,
                successmod: 95,
                crawledCount: 50,
                historyCount: 5000,
                xcCount: 5,
                totalTime: 200,
                //----test------auto roll-----------
                displayedUsers: [],
                scrollDuration: 5000, // 滚动持续时间（毫秒）
                scrollInterval: null,
                isAutoScrollEnabled: true, // 控制自动滚动的开关
                recentUsers: [
                    { name: '用户1', type: '类型1', time: '2024-02-23 10:30:00' },
                    { name: '用户2', type: '类型2', time: '2024-02-23 10:35:00' },
                    { name: '用户3', type: '类型3', time: '2024-02-23 10:40:00' },
                    { name: '用户4', type: '类型4', time: '2024-02-23 10:45:00' },
                    { name: '用户5', type: '类型5', time: '2024-02-23 10:50:00' },
                    { name: '用户6', type: '类型6', time: '2024-02-23 10:55:00' },
                    { name: '用户7', type: '类型7', time: '2024-02-23 11:00:00' },
                    { name: '用户8', type: '类型8', time: '2024-02-23 11:05:00' }
                    // 更多用户数据...
                ],
                //------------切换文本框-------------
                texts: [
                    "押忍！战斗！应援团是任天堂 DS 的音乐节奏游戏。游戏只有三个元素：点击触摸屏上的圆圈，在指定的“滑条”上拖着一个球，以及快速旋转转盘。所有这些元素都是契合了流行日文歌曲的节奏。在 DS 上看起来像这样：",
                    "上图是转盘。单击并按住鼠标，然后快速旋转（顺时针、逆时针都可以）转盘，直到转盘外侧的圆圈完全发光为止。完成转盘时，转盘上方也会出现 Clear 的提示。如果完成转盘后还有剩余时间，继续旋转可以获得额外分数，也会帮助回血。转盘外侧的白色圆圈会告诉你完成转盘的剩余时间。当时间不足时，圆圈会变红。有些谱面使用的是皮肤版本 1.0，此时转盘两侧有量计，指示转盘完成的程度。转盘下方有一个小方框，显示的是按目前速度持续旋转的情况下，每分钟可完成的转数。如果过早松开了按键（或者笔离开了数位板），就会停止读数。玩 DS 时（尤其是高难度关卡），完成转盘很容易刮坏触摸屏（或者屏幕保护膜）。osu! 的转盘已经优化，完成它不会这么麻烦。转盘每分钟可实现的最高转数是 477 转，也是 Auto 模组游玩时的旋转速度，而 Spun Out 模组的旋转速度较低，是每分钟 287 转。在转盘中按下按键时，光标会“锁”进转盘旋转区域内，所以你大可以试着抓狂般的旋转，又或者慢慢转小圈圈——随您所好。在此建议你边看他人的回放边练习。左上角是血条，血量会随时间减少（取决于谱面的难度设定），但是可以通过在恰当的时间打击物件、完成转盘来回复血量。准确的打击（获得 300 或“激”）回复的血量比不准确的打击（获得 50）多，而一个失误足以造成血量大跌。右上角显示的是分数，下方显示准确度。准确度旁边（分数下方）是谱面时长的计时器。左下角的数字显示连击数，也是得分乘数。",
                    "尽管大多数玩家可能没有触摸屏，但此游戏可用常规鼠标游玩，使用任何输入设备的创意组合也可以。不同的玩法可在此页面找到。有些歌曲会推动鼠标与玩家的极限，但所有（上架 (Ranked) 和通过 (Approved)）的谱面都已经测试，（大多数）都可以只用鼠标过关。"
                ],
                currentIndex: 0,
                intervalId: null,
                intervalDuration: 3000, // 文本切换间隔，单位为毫秒

                //-----接替瀑布图---------
                visitData: [900, 345, 393, -108, -154, 135, 178, 286, -119, -361, -203, 100, 310, 212], // 十四天内的访问量数据

                //----数据来源top10-----------
                provinceData: [
                    { name: '广东', value: 100 },
                    { name: '北京', value: 200 },
                    { name: '上海', value: 300 },
                    { name: '湖北', value: 400 },
                    { name: '河南', value: 500 },
                    { name: '山东', value: 600 },
                    { name: '江西', value: 700 },
                    { name: '河南', value: 800 },
                    { name: '湖南', value: 900 },
                    { name: '四川', value: 1000 }
                ],

            };
        },
        //----文本框用--------
        computed: {
            currentText() {
                const originalText = this.texts[this.currentIndex];
                if (originalText.length > this.maxCharacters) {
                    return originalText.substring(0, this.maxCharacters) + '...';
                } else {
                    return originalText;
                }
            }
        },//------------------
        mounted() {
            this.renderCharts();
            // this.renderLineChart();
            //-------轮播列表------
            if (this.isAutoScrollEnabled) {
                this.startScroll();
            }
            //--------文本框-------
            this.startTextRotation();
            //--------阶梯瀑布图---------
            this.renderVisitChart();
            //-------数据来源top10------
            this.renderChart();
        },
        methods: {
            renderCharts() {
                // 今日爬取数据类型统计图表
                var sectionDataChart = echarts.init(this.$refs.sectionDataChart);
                var sectionDataOption = {
                    tooltip: {
                        trigger: 'item',
                        formatter: '{a} <br/>{b}: {c} ({d}%)'
                    },
                    series: [
                        {
                            name: '访问来源',
                            type: 'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                show: false,
                                position: 'center'
                            },
                            emphasis: {
                                label: {
                                    show: true,
                                    fontSize: '30',
                                    fontWeight: 'bold'
                                }
                            },
                            labelLine: {
                                show: false
                            },
                            data: [
                                { value: 335, name: '体育' },
                                { value: 310, name: '娱乐' },
                                { value: 234, name: '科技' },
                                { value: 135, name: '财经' },
                                { value: 1548, name: '其他' }
                            ]
                        }
                    ]
                };
                sectionDataChart.setOption(sectionDataOption);

                // 过去几天爬取数据量趋势
                var pastDaysChart = echarts.init(this.$refs.pastDaysChart);
                var pastDaysOption = {
                    xAxis: {
                        type: 'category',
                        data: ['2024-02-20', '2024-02-21', '2024-02-22', '2024-02-23', '2024-02-24', '2024- 02-25', '2024-02-26']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [120, 200, 150, 80, 100, 150, 200],
                        type: 'line'
                    }]
                };
                pastDaysChart.setOption(pastDaysOption);

                // 爬取数据量 Top N 板块
                var topSectionsChart = echarts.init(this.$refs.topSectionsChart);
                var topSectionsOption = {
                    xAxis: {
                        type: 'category',
                        data: ['体育', '娱乐', '科技', '财经', '其他']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [200, 150, 300, 180, 250],
                        type: 'bar'
                    }]
                };
                topSectionsChart.setOption(topSectionsOption);

                //24h 折线
                // 模拟数据
                const hours = ['00:00', '01:00', '02:00', '03:00', '04:00', '05:00', '06:00', '07:00', '08:00', '09:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00'];
                const successData = [120, 200, 150, 80, 100, 220, 300, 180, 150, 200, 250, 320, 280, 260, 180, 200, 230, 270, 300, 320, 280, 250, 200, 180];
                const failData = [30, 50, 40, 20, 25, 60, 80, 40, 30, 50, 70, 90, 80, 70, 60, 50, 40, 60, 80, 100, 90, 70, 50, 40];

                // 初始化图表
                var lineChart = echarts.init(this.$refs.lineChart, { width: '500px' });

                // 图表配置
                var option = {
                    title: {
                        text: '24小时爬取统计'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data: ['成功', '失败']
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: hours
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        {
                            name: '成功',
                            type: 'line',
                            data: successData,
                            itemStyle: {
                                color: 'green'
                            }
                        },
                        {
                            name: '失败',
                            type: 'line',
                            data: failData,
                            itemStyle: {
                                color: 'orange',
                            }
                        }
                    ]
                };

                // 使用刚指定的配置项和数据显示图表
                lineChart.setOption(option);

            },
            startScroll() {
                this.displayedUsers = [...this.recentUsers];
                const userList = this.$refs.userList;
                const scrollStep = () => {
                    const currentScrollTop = userList.scrollTop;
                    const newScrollTop = currentScrollTop + 1; // 每次滚动一个像素
                    if (newScrollTop >= userList.scrollHeight) {
                        userList.scrollTop = 0; // 滚动到顶部
                    } else {
                        userList.scrollTop = newScrollTop;
                    }
                };
                this.scrollInterval = setInterval(scrollStep, 50); // 设置间隔时间，单位为毫秒
            },

            stopScroll() {
                this.isAutoScrollEnabled = false; // 停止自动滚动
                clearInterval(this.scrollInterval);
            },

            //------------文本框--------
            startTextRotation() {
                this.intervalId = setInterval(() => {
                    this.currentIndex = (this.currentIndex + 1) % this.texts.length; // 循环切换文本
                }, this.intervalDuration);
            },
            stopTextRotation() {
                clearInterval(this.intervalId);
            },
            beforeDestroy() {
                this.stopTextRotation();
            },

            //-------接替瀑布图---------
            renderVisitChart() {
                const visitChart = echarts.init(this.$refs.visitChart);
                const option = {
                    title: {
                        text: '十四天内平台访问量变化',
                    },
                    xAxis: {
                        type: 'category',
                        data: Array.from({ length: this.visitData.length }, (_, i) => `第${i + 1}天`),
                    },
                    yAxis: {
                        type: 'value',
                    },
                    series: [{
                        type: 'bar',
                        data: this.visitData.map((value) => Math.max(value, 0)),
                        itemStyle: {
                            color: 'blue', // 正数部分使用蓝色表示
                        },
                    }, {
                        type: 'bar',
                        data: this.visitData.map((value) => Math.min(value, 0)),
                        itemStyle: {
                            color: 'red', // 负数部分使用红色表示
                        },
                    }],
                };
                visitChart.setOption(option);
            },

            //------数据来源top10-----------

            renderChart() {
                const provinceChart = echarts.init(this.$refs.provinceChart);
                const option = {
                    title: {
                        // text: '数据来源省份 Top 10',
                        left: 'center',
                        top: 'top'
                    },
                    tooltip: {
                        trigger: 'item',
                        formatter: '{b}: {c} (次)'
                    },
                    series: [
                        {
                            name: '访问次数',
                            type: 'pie',
                            radius: '50%',
                            data: this.provinceData.map(item => ({
                                name: item.name,
                                value: item.value
                            })),
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
                provinceChart.setOption(option);
            },
        }

    };
</script>

<style scoped>
    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow: auto;
    }

    .top-boxes {
        display: flex;
        justify-content: space-around;
        margin-bottom: 20px;
    }

    .box {
        border: 1px solid #ccc;
        border-radius: 5px;
        padding: 10px;
        text-align: center;
    }

    .middle-section {
        display: flex;
        justify-content: space-around;
        margin-bottom: 20px;
    }

    .left-box,
    .right-box {
        flex: 1;
    }

    .inner-box {
        border: 1px solid #ccc;
        border-radius: 5px;
        padding: 20px;
    }

    .chart {
        height: 300px;
    }

    .spider-status {
        margin-bottom: 20px;
    }

    .spider-status .inner-box {
        border: 1px solid #ccc;
        border-radius: 5px;
