<template>
    <div class="container">
        <div class="column_1" style="margin-right: .0625rem; display: flex; flex-direction: column;">
            <div style="flex: 2; margin-bottom: .25rem;">
                <el-table :data="tableData" border style="width: 100%; height: 100%;" :header-cell-style="getRowClass">
                    <el-table-column prop="rank" label="排名" width="60" align="center" />
                    <el-table-column prop="category" label="热点事件类别" width="130" />
                    <el-table-column prop="key_word" label="热点事件关键词" />
                    <el-table-column prop="browse_number" label="热度值" width="100" />
                </el-table>
            </div>
            <div style="display: flex; flex: 1;">
                <DataChart :chartTitle="`用户情感分析`" :chartOption="barOption" :chartId="`chart_2`" class="emotionAnalyse"
                    :imageHeight="`5rem`" style="flex: 1;" />
                <DataChart :chartTitle="`热点词云分析`" :chartOption="UserInterest" :chartId="`chart_1`" :imageHeight="`5rem`"
                    style="flex: 1;" />
            </div>
        </div>
        <div class="column_2" style="margin-left: .0625rem;display: flex; flex-direction: column;">
            <div class="no">
                <div class="no-hd">
                    <ul>
                        <li>127899</li>
                        <li>2501278</li>
                    </ul>
                </div>
                <div class="no-bd">
                    <ul>
                        <li>实时互动率(User Interaction Rate)/ 时</li>
                        <li>实时阅读量(Influence Score)/ 时</li>
                    </ul>
                </div>
            </div>
            <div class="map">
                <ChinaMap></ChinaMap>
            </div>
        </div>
    </div>

</template>

<script setup>
    import { ref, reactive, shallowRef, onMounted, onBeforeUnmount } from 'vue'
    import DataChart from '@/components/DataChartPx.vue'
    import configOption from '@/ChartOption/optionConfig.js'
    import hotpotOption from '@/ChartOption/HotChina.js'
    import ChinaMap from '@/components/ChinaMap.vue'
    const UserInterest = shallowRef(configOption.UserInterest)

    // const tableData = reactive(hotpotOption.tableData)
    const tableData = ref()
    const timerInstance = shallowRef(null)
    const infoIndex = ref(0)

    onMounted(() => {
        const newArray = hotpotOption.tableData.slice(0, 10)
        tableData.value = newArray
        if (timerInstance.value) { clearInterval(timerInstance.value) }
        timerInstance.value = setInterval(() => {
            changeInfo(infoIndex.value)
        }, 3000)
    })
    onBeforeUnmount(() => {
        if (timerInstance.value) { clearInterval(timerInstance.value) }
    })

    function changeInfo(index) {
        infoIndex.value = (infoIndex.value + 1) % 5
        const newArray = hotpotOption.tableData.slice(infoIndex.value * 10, infoIndex.value * 10 + 10)
        console.log
        console.log(newArray)
        tableData.value = newArray

    }

    const chinaMap = shallowRef(hotpotOption.chinaMap)
    const barOption = shallowRef(hotpotOption.barOption)
    //改变表格头部样式
    function getRowClass({ row, column, rowIndex, columnIndex }) {
        return {
            background: '#03487B',
            color: '#1adafb'
        }
    }
</script>

<style lang="scss" scoped>
    .container {
        background: url('@/assets/newbg_1.png') no-repeat top center;
        background-size: 100% 100%;
        height: 100%;
        width: 100%;
        line-height: 1.15;
        padding: .25rem .1875rem 0;
        display: flex;
        overflow: auto;
    }

    .column_1 {
        width: 40%;
    }

    .column_2 {
        width: 60%;
    }

    /* no数字模块设计 */
    .no {
        background: rgba(101, 132, 226, 0.1);
        padding: .1875rem;

        .no-hd {
            border: 1px solid rgba(25, 186, 139, 0.17);
            position: relative;

            &::before {
                position: absolute;
                top: 0;
                left: 0;
                content: "";
                width: 30px;
                height: 10px;
                border-left: 2px solid #02a6b5;
                border-top: 2px solid #02a6b5;
            }

            &::after {
                position: absolute;
                bottom: 0;
                right: 0;
                content: "";
                width: 30px;
                height: 10px;
                border-bottom: 2px solid #02a6b5;
                border-right: 2px solid #02a6b5;
            }

            ul {
                display: flex;

                li {
                    flex: 1;
                    text-align: center;
                    line-height: 1rem;
                    font-size: .875rem;
                    color: #ffeb7b;
                    position: relative;
                    /* font-family: "TypoDigitDemo-VG9lV"; */
                    list-style-type: none;

                    &::after {
                        content: "";
                        position: absolute;
                        top: 25%;
                        height: 50%;
                        right: 0;
                        width: 1px;
                        background: rgba(255, 255, 255, 0.2);
                    }
                }
            }
        }

        .no-bd {
            ul {
                display: flex;

                li {
                    flex: 1;
                    text-align: center;
                    height: .5rem;
                    line-height: .5rem;
                    color: rgba(255, 255, 255, 0.7);
                    font-size: .225rem;
                    padding-top: .125rem;
                    list-style-type: none;
                }
            }
        }
    }

    ::v-deep .el-table,
    .el-table__expanded-cell {
        background-color: transparent;
        color: white;
    }

    ::v-deep .el-table tr {
        background-color: transparent !important;
    }

    ::v-deep .el-table tbody tr:hover>td {
        background-color: rgba(3, 72, 122, 0.5) !important;
    }

    ::v-deep .el-table–enable-row-transition .el-table__body td,
